<template>
<div class="cardInMoneyHis">
    <topNav title="充值历史" url="/cardInMony"></topNav>
    <div class="dateAll">
        <div class="dataBox">
            <div class="data">
                <datepicker v-model="range" class="picker picker-range" :min="start" :max="end" :range="true"></datepicker>
            </div>
            <div class="line"></div>
            <div class="allBox">
                共 <b>{{masterTotal}}</b> 笔
            </div>
        </div>
        <div class="searchBox">
            <div class="search">
                <span>余额：<b>{{sumInfo.accountBalance | tofixed}}</b>元</span> <span>信用：<b>{{sumInfo.accountCredit | tofixed}}</b>元</span> <span>冻结：<b>{{sumInfo.accountFreeze | tofixed}}</b>元</span>
            </div>
        </div>
    </div>
    <div class="scrollBox" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
        <div class="orderList" v-for='(item,index) in listInfo' :key='index'>
            <div class="blueLind"></div>
       
            <div class="infoL">
                <div class="lineInfo">
                    <div class="titleThird nomalL">项目</div>&nbsp;:
                    <span>{{item.taDescript}}</span>
                </div>
                <div class="lineInfo">
                    <div class="titleThird nomalL">金额</div>&nbsp;:
                    <span>{{item.amount}}</span>
                </div>
                <div class="lineInfo">
                    <div class="titleThird nomalL">余额</div>&nbsp;:
                    <span>{{item.balanceUsable}}</span>
                </div>
                <div class="lineInfo">
                    <div class="titleThird nomalL">时间</div>&nbsp;:
                    <span>{{item.createDate}}</span>
                </div>
            </div>
            <div class="infoL infoR">
                <div class="lineInfo">
                    <div class="titleThird nomalL">账务类型</div>&nbsp;:
                    <span>{{item.accountType | niming}}</span>
                </div>
                <div class="lineInfo">
                    <div class="titleThird nomalL">房号</div>&nbsp;:
                    <span>{{item.roomNo}}</span>
                </div>
                <div class="lineInfo">
                    <div class="titleThird nomalL">备注</div>&nbsp;:
                    <span>{{item.remark}}</span>
                </div>
                
            </div>

        </div>
        <div v-bind:class="{ 'hide': !isEmpty }">
            <img class="nothing" src="../../assets/img/nothing.png">
            <p class="nothingTxt">什么都没有</p>
        </div>
        <div class="infoWarn" v-if='cutOff==1'> —— 我是有底线的 —— </div>
    </div>
</div>
</template>

<script>
import moment from "moment";
import Datepicker from "../common/Datepicker";
import {
    InfiniteScroll,
    Actionsheet,
    Toast
} from "mint-ui";

export default {
    data() {
        return {
            cutOff: "",
            isEmpty: true,
            masterTotal: 0,
            start: moment().add(-60, "M").format("YYYY-MM-DD"),
            end: moment().add(1,'day').format("YYYY-MM-DD"),
            range: [moment().add(-1, "M").format("YYYY-MM-DD"), moment().add(1,'day').format("YYYY-MM-DD")],
            listInfo: [],
            sumInfo: {
                accountBalance: 0,
                accountFreeze: 0,
                accountCredit: 0
            },
            searchOption: {
                beginDate: null,
                endDate: null,
                cardNo: "",
                cardId: "",
                maxResults: 10,
                firstResult: 0
            },
        };
    },
    computed: {},
    mounted() {
        if (this.$route.query.cardNo) {
            this.searchOption.cardNo = this.$route.query.cardNo;
            this.searchOption.cardId = this.$route.query.cardId;
            this.getInfo();
        }

    },
    filters: {
        tofixed(v) {
            return parseFloat(v).toFixed(2);
        },
        niming(v) {
            if (v && v.length > 4) {
                return v.substr(0, 2) + "***" + v.substr(v.length - 2);
            }
            return v;
        },
        fd(value) {
            if (value == null || value == "") return "";
            return value.substr(11);
        }
    },
    methods: {
        search() {
            //查询的时候充值分页
            this.searchOption.firstResult = 0;
            this.listInfo = [];
            this.getInfo();

        },

        dateChange(value) {
            this.searchOption.beginDate = moment(value).format("YYYY-MM-DD");
            this.searchOption.endDate = moment(value).add(1, 'day').format("YYYY-MM-DD");
            this.listInfo = [];
            this.search();
        },

        getInfo(isloadMore) {
            this.cutOff = 0;

            this.searchOption.beginDate = this.range[0];
            this.searchOption.endDate = moment(this.range[1]).format("YYYY-MM-DD");
            this.$http
                .post("/pos/posUnion/memberPayRecord", this.searchOption)
                .then(res => {
                    if (res.data.errorCode == 0) {

                        if (res.data.result.resultCode == 0) {
                            var m = res.data.result;
                            this.masterTotal = m.totalRows;
                            this.sumInfo.accountBalance = m.accountBalance;
                            this.sumInfo.accountFreeze = m.accountFreeze;
                            this.sumInfo.accountCredit = m.accountCredit;
                            this.isEmpty = false;
                            if (isloadMore) {
                                // 加载更多
                                _.forEach(m.accountList, (el, key) => {
                                    this.listInfo.push(el);
                                });
                            } else {
                                // 第一次加载数据
                                this.listInfo = m.accountList;
                            }
                            //判断是否已经到底了的

                            if (this.masterTotal >= 4 && this.masterTotal <= this.searchOption.firstResult + this.searchOption.maxResults) {
                                this.cutOff = 1;
                            }

                            this.loading = false;
                            if (this.masterTotal == "0") {
                                //this.msg('查询结果为0笔')
                                this.isEmpty = true;
                                this.cutOff = 0;
                            }
                        } else {
                            this.msg(res.data.result.resultMsg);
                        }
                    } else {
                        this.msg(res.data.errorMsg);
                    }
                });
        },
        msg(msg) {
            Toast({
                message: msg,
                position: "bottom",
                duration: 3000
            });
        },
        loadMore() {
            // 自动加载内容
            //console.log("loadMore")
            if (
                this.masterTotal >=
                parseInt(this.searchOption.maxResults) +
                parseInt(this.searchOption.firstResult)
            ) {
                this.searchOption.firstResult += this.searchOption.maxResults;
                this.loading = true;
                let sef = this;
                var isloadMore = true;
                sef.getInfo(isloadMore);
            }
        }
    },
    components: {
        actionsheet: Actionsheet,
        Datepicker
    }
};
</script>

<style lang="less">
.cardInMoneyHis {
    text-align: center;
    overflow-y: hidden;

    .dateAll {
        width: 100%;
        height: 2.88rem;
        background: #fff;
        position: fixed;
        margin-top: -1px;
        z-index: 2000;

        .dataBox {
            margin: 0 auto;
            width: 96%;
            height: 100%;

            .data {
                width: 65%;
                height: 1.0667rem;
                margin-top: 0.1067rem;
                display: inline-block;

                .date-picker {
                    margin-top: 0.1rem;
                    margin-left: .4rem;
                }
            }

            .line {
                width: 0.0267rem;
                text-align: center;
                display: inline-block;
                vertical-align: top;
                margin-top: 0.3733rem;
                height: 0.5867rem;
                background: #cccccc;
            }

            .allBox {
                width: 30%;
                margin-top: 0.48rem;
                height: 1.0667rem;
                display: inline-block;
                color: #393c41;
                font-size: 0.37rem;
                vertical-align: top;

                b {
                    color: red;
                }
            }
        }

        .searchBox {
            height: 1.6rem;
            margin: 0 auto;
            width: 100%;
            position: fixed;
            background: #f4f5f7;
            margin-top: -1px;
            top: 2.3733rem;

            .search {
                width: 94%;
                margin: 0 auto;
                height: 0.8rem;
                padding-top: 0.4rem;
                span{
                    margin-right:0.4rem;
                    b{color:red;}
                }
            }
        }
    }

    .scrollBox {
        position: fixed;
        margin-top: 2.9rem;
        width: 100%;
        height: 79%;
        overflow-y: auto;
        padding-bottom: 2rem;

        .orderList {
            background: #ffffff;
            margin-bottom: 0.2667rem;
            width: 96%;
            height: auto;
            padding-bottom: 0.2667rem;
            margin: 0 auto 0.2667rem;
            box-shadow: 0 0 0.2133rem 0rem #dadada;
            border-radius: 0.1333rem;
            position: relative;

            .mintui {
                position: absolute;
                top: 30%;
                right: 5%;
                transform: rotate(180deg);
            }

            .top {
                width: 100%;
                height: 0.5rem;
                line-height: 0.5rem;
                padding-top: 0.3rem;
                text-align: left;
                margin-bottom: -0.4rem;
                margin-left: 0.2rem;
            }

            .blueLind {
                height: 0.3733rem;
                width: 1.1%;
                background: #646f83;
                display: inline-block;
                float: left;
                margin-top: 0.48rem;
            }

            .infoL {
                width: 49%;
                height: auto;
                text-align: left;
                display: inline-block;
                padding-top: 0.3733rem;

                .lineInfo {
                    margin: 0.1067rem auto;
                    color: #999999;

                    .titleThird {
                        display: inline-block;
                        color: #999999;
                        letter-spacing: 0.2667rem;
                    }

                    .titileS {
                        display: inline-block;
                        color: #999999;
                    }

                    .nomalL {
                        letter-spacing: 0rem;
                    }

                    span {
                        margin-left: 0.1333rem;
                        font-size: 0.32rem;
                        color: #151515;
                    }
                }
            }

            .infoR {
                width: 45%;
            }

            .infoBo {
                text-align: left;
                margin-left: 0.3733rem;

                .titleThird {
                    display: inline-block;
                    color: #999999;
                    letter-spacing: 0.2667rem;
                }

                .titileS {
                    display: inline-block;
                    color: #999999;
                }

                .nomalL {
                    letter-spacing: 0rem;
                }

                span {
                    margin-left: 0.1333rem;
                    font-size: 0.3733rem;
                    color: #151515;
                }

                b {
                    font-size: 0.32rem;
                    color: #4990e2;
                    text-decoration: underline;
                    margin-left: 0.1333rem;
                }
            }
        }
    }

    .nothing {
        width: 80px;
        height: 88px;
        margin: 150px auto 0 auto;
    }

    .nothingTxt {
        text-align: center;
        font-size: 20px;
        color: #ccd1d5;
        margin-top: 20px;
    }

    .show {
        display: block;
    }

    .hide {
        display: none;
    }

    .mint-popup-right {
        width: 100%;
        height: 100%;
    }

    .infoWarn {
        height: 50px;
        color: #ccc;
    }
}
</style>



